.cmn-toggle-switch {
  display: block;
  position: relative;
  overflow: hidden;
  margin-left: 20px;
  padding: 0;
  width: 40px;
  height: 40px;
  font-size: 0;
  text-indent: -9999px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  box-shadow: none;
