  border: 1px solid #ccc; }

.cmn-toggle-switch:focus {
  outline: none; }

.cmn-toggle-switch span {
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
  left: 5px;
  right: 5px;
  height: 3px;
  background: #333; }

.cmn-toggle-switch span::before,
.cmn-toggle-switch span::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #333;
  content: ""; }

.cmn-toggle-switch span::before {
  top: -10px; }

.cmn-toggle-switch span::after {
  bottom: -10px; }

/**
 * Style 2
